<template>
<div class="recommend">
  <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item, index) in imgs">
          <img v-lazy="item.img" >
      </mt-swipe-item>
  </mt-swipe>
  <div class="movieList">
    <detail :dynamic="list"></detail>
  </div>
</div>
</template>

<script>
import detail from '@/components/detail'
export default {
  data () {
    return {
      imgs: [
        {img: 'http://tupian.aladd.net/2014/3/1/2061.jpg', author: '红鲫鱼'},
        {img: 'http://tupian.aladd.net/2014/3/1/2062.jpg', author: '红鲫鱼'},
        {img: 'http://tupian.aladd.net/2014/3/1/2062.jpg', author: '红鲫鱼'},        
      ],
      list: []
    }
  },
  created () {
    axios.get('/api/data').then(res => {
            this.list = res.data.travelLists
        }).catch(err => {
            console.log(err)
        })
  },
  computed: {
  },
  components: {
    detail
  }
}
</script>

<style lang="stylus">
.mint-swipe
  height 4.277rem
  width 10rem
  border-bottom 0.025rem solid #cdcdcd
  img[lazy=loading]
    width 100%
    height 100%
  img[lazy=loaded]
    width 100%
    height 100%

</style>


